export type FortressData = {
  name: string;
  hash: string;
  x: string;
  y: string;
  blockNumber: number;
};

export const findFortress = (x: string, y: string) => {
  return metadata.find((f) => f.x === x && f.y === y);
};

export const findFortressNumber = (x: string, y: string) => {
  return metadata.findIndex((f) => f.x === x && f.y === y) + 1;
};

export const calculateNameRarity = (name: string) => {
  const names = metadata.map((f) => f.name);
  const result = names.filter((n) => n === name).length;
  return `${result / 5}%`;
};

export const calculateXRarity = (x: string) => {
  const names = metadata.map((f) => f.x);
  const result = names.filter((n) => n === x).length;
  return `${result / 5}%`;
};

export const calculateYRarity = (y: string) => {
  const names = metadata.map((f) => f.y);
  const result = names.filter((n) => n === y).length;
  return `${result / 5}%`;
};

export const calculateDistance = (x: string, y: string) => {
  return Math.max(Math.abs(parseInt(x)), Math.abs(parseInt(y)));
};

export const calculateDistanceRarity = (x: string, y: string) => {
  const distance = calculateDistance(x, y);
  const fortressesAtSameDistance = 4 * 2 * distance;
  return `${fortressesAtSameDistance / 5}`;
};

export const calculateYear = (blockNumber: number) => {
  if (blockNumber < 4826545) {
    return 2017;
  } else if (blockNumber < 4832456) {
    return 2018;
  } else if (blockNumber < 6988382) {
    return 2019;
  } else if (blockNumber < 9193047) {
    return 2020;
  } else {
    return 2021;
  }
};

